.box , .passive , .active{
    display: flex;
    width: 100%;
    padding:13px  14px  13px 13px;
    background: #5932EA;
    border-radius: 8px;
    align-items: center;
    width: 250px;
    /* max-width: 250px; */
    justify-content: space-between;
    margin: 18px 0 0 0;
}
.passive{
    background: #fff;
    color: rgba(145, 151, 179, 1);
    transition: all .2s;
}
.passive:hover{
    background: #d5cfeb;
}
.box p{
    font-weight: 500;
    font-size: 14px;
    color: #FFFFFF;
    margin-left: 15px;
}
.active{
    background: #5932EA;
    color: #fff;
}
